
.wb-mask {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  display: none;
}
.wb-mask.active {
  display: table;
}
.wb-mask .wb-mask-item {
  max-width: 800px;
  margin: auto;
  vertical-align: middle;
  margin-top: 10%;
  border-radius: 4px;
  overflow: hidden;
}
.floor-box {
  padding: 5px;
  margin-bottom: 15px;
  background-color: #eaeaea;
  border-radius: 5px;
  color: #435964;
}
.floor-box h4 {
  margin: 0px;
}
.floor-box.active {
  background-color: #36a9e1;
  color: #fff;
}
.floor-panel {
  position: relative;
  width: 100%;
}
.floor-panel img {
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  background-color: #000;
}
.upload-panel {
  background-color: #eee;
  margin-top: 5px;
  padding: 7px;
  border-radius: 5px;
}
.camera-item {
  padding: 15px;
  background-color: #f1f1f1;
  border-radius: 5px;
  margin-bottom: 15px;
}
.camera-item h5 {
  margin: 0px;
}
.camera-info label {
  padding: 5px;
  background-color: #cfcfcf;
  margin: 5px;
  border-radius: 5px;
}
.camera-info label span {
  font-weight: bolder;
  color: #00557e;
  border: 1px solid #aaa;
  padding: 0px 5px;
  border-radius: 5px;
}
.wb-mask .camera-mask-item {
  max-width: 1280px;
  width: 95%;
  margin-top: 20px;
}
.camera-info,
.camera-info-box {
  margin-top: 15px;
}
.cemera-info-block {
  max-height: 80vh;
  overflow: auto;
}
.map-info-block {
  max-height: 60vh;
  overflow: auto;
}
.camera-position {
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: #37d8bf;
  border: 1px solid #1ead97;
  box-shadow: 0px 0px 5px 10px #a2f5ff8a;
  border-radius: 50px;
}
.camera-position:after {
  position: absolute;
  content: "";
  border: 60px solid transparent;
  border-right: 160px solid rgba(55, 216, 191, 0.54);
  margin-top: -37px;
  margin-left: -59px;
}



